.single-blog-wrapper * {
  margin: revert;
  padding: revert;
  list-style-type: revert;
}
.single-blog-wrapper code, .single-blog-wrapper pre {
  border-radius: 5px;
  background-color: #f6f8fa;
  color: inherit;
}
.single-blog-wrapper code {
  padding: 1px 5px;
}
.single-blog-wrapper pre {
  border: 1px solid #CCCCCC;
  line-height: 1.25em;
  overflow: auto;
  padding: 6px 10px;
}
.single-blog-wrapper blockquote {
  margin: 0;
  border-left: 5px solid #7a7a7a;
  font-style: italic;
  padding: 1.33em;
  text-align: left;
}

